import { Component, Element, FunctionalComponent, h, Host, Listen, Prop, State } from '@stencil/core';
import { GridColumnCount } from '../../../interfaces/components/layout/columns/grid';
import { DocumentUtils } from '../../../utils/document/document-utils';

@Component({
  tag: 'rwc-icon-text-steps',
  styleUrl: './icon-text-steps.scss',
  shadow: true
})
export class RwcIconTextSteps {
  /**
   * The class used for vertical layout of the component.
   */
  static VERTICAL_LAYOUT_CLASS: string = 'vertical';

  /**
   * Reference to the host element of the component.
   */
  @Element() host: HTMLRwcIconTextStepsElement;

  /**
   * Number of columns.
   */
  @Prop() columns: GridColumnCount = 4;

  /**
   * Updates the layout automatically if the minimum content width is smaller than the host element.
   */
  @Prop() autoUpdateLayout: boolean = true;

  /**
   * The column count determined by the auto layout.
   */
  @State() autoColumnCount: number = 4;

  /**
   * Parses the data on window resize.
   */
  @Listen('resize', { target: 'window' })
  onWindowResize(): void {
    this.updateLayout();
  }

  /**
   * Updates the layout on completing the first load of the component.
   */
  componentDidLoad(): void {
    this.updateLayout();
  }

  /**
   * Updates the column count based on the minimum step width and column gap.
   */
  private updateLayout(): void {
    if (this.autoUpdateLayout) {
      const stepCount = this.host.children.length;

      if (stepCount > 0) {
        const minCardWidth = DocumentUtils.convertToPixels(getComputedStyle(this.host).getPropertyValue('--icon-text-steps-card-width'));
        const gridGapSize = DocumentUtils.convertToPixels(getComputedStyle(this.host).getPropertyValue('--icon-text-steps-grid-column-gap'));
        const minHostSize = stepCount * minCardWidth + (stepCount - 1) * gridGapSize;

        let hostWidth = this.host.offsetWidth
        if (typeof hostWidth === 'undefined') {
          hostWidth = window.innerWidth;
        }

        if (hostWidth >= minHostSize) {
          this.autoColumnCount = stepCount;
          this.host.classList.remove(RwcIconTextSteps.VERTICAL_LAYOUT_CLASS);
        } else {
          this.autoColumnCount = 1;
          this.host.classList.add(RwcIconTextSteps.VERTICAL_LAYOUT_CLASS);
        }
      } else {
        this.autoColumnCount = 4;
      }
    }
  }

  /**
   * Renders the component.
   */
  render(): FunctionalComponent {
    return (
      <Host>
        <div class="container">
          <rwc-grid
            class="grid"
            columns={ this.autoUpdateLayout ? this.autoColumnCount : this.columns }
          >
            <slot />
          </rwc-grid>
        </div>
      </Host>
    );
  }
}
