@import 'global.scss';

:host {
  /**
   * @prop --icon-text-steps-grid-column-gap: The gap between the columns of the grid.
   * @prop --icon-text-steps-grid-columns-row-gap: The gap between the rows of the grid.
   *
   * @prop --icon-text-steps-card-width: The width of the step card.
   *
   * display: The display mode of the host element.
   */
  --icon-text-steps-grid-column-gap: #{$rwc-spacing--lg};
  --icon-text-steps-grid-columns-row-gap: #{$rwc-spacing--lg};

  --icon-text-steps-card-width: 10.75rem;

  display: block;
}

.container {
  position: relative;
}

.grid {
  --grid-column-gap: var(--icon-text-steps-grid-column-gap);
  --grid-row-gap: var(--icon-text-steps-grid-columns-row-gap);
}

::slotted(*) {
  width: var(--icon-text-steps-card-width);
  margin-left: auto;
  margin-right: auto;
}
